a:link {
  text-decoration: none;
}

hr {
  color: lightgray;
  margin: 50px;
}


* {
    font-family: 'Roboto Slab', serif;
    font-weight: bold;
}

a:link {
    text-decoration: none;
}

.header {
    display: flex;
    justify-content: space-around;
    padding-top: 20px;
    padding-bottom: 10px;
    /* position: fixed; */
    /* top: 0; */
    width: 100%;
    z-index: 100;
    background-color: white;
    height: 50px;
    align-items: center;
    overflow: auto;
}

.header__choiceSection {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.header__choice {
    padding: 10px;
    color: black;
}

.header__choice:hover {
    cursor: pointer;
    border-bottom: 1px double black; 
    -webkit-animation-name: header__choice_jump; 
            animation-name: header__choice_jump;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

@-webkit-keyframes header__choice_jump {
    0%   {padding-top: -10px;}
    100% {padding-top: 0;}
}

@keyframes header__choice_jump {
    0%   {padding-top: -10px;}
    100% {padding-top: 0;}
}

.footer {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    bottom: 0px;
    position: -webkit-sticky;
    position: sticky;
    background-color: white;
    text-align: center;
    z-index: 100;
}


.footer > p > span {
    color: #233FAA;
    font-weight: bold;
}

.hero {
    background-image: url(https://res.cloudinary.com/dfeh7lczz/image/upload/v1626847905/CTnbfCtg_wjrle5.jpg);
    min-height: 400px;
    background-attachment: fixed;
    background-size: cover;
    position: relative;
}


.hero__welcome {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.hero__welcome > p {
    font-family: 'Titillium Web', sans-serif;
    color: white;
    letter-spacing: 10px;
    font-size: 20px;
}
.about {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about__title {
    text-align: center;
    font-size: 50px;
    margin-top: 100px;
}

.about__title > span {
    color: #233FAA;
}

.about__box {
    display: flex;
    width: 60%;
    margin-top: 20px;
}

.about__image {
    max-width: 300px;
    object-fit: contain;
    flex: 0.3 1;
    border-radius: 2px;
}

.about__text {
    flex: 0.7 1;
    position: relative;
    margin: auto;
    height: 100%;
    width: 50%;
    margin-right: 30px;
    margin-left: 30px;    
    font-family: 'Inconsolata', monospace;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.about__textGroupA {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    flex: 0.8 1;
    margin-bottom: 60px;
}

.about__textGroupB {
    flex: 0.2 1;
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 5px;
    color: #007FC5;
}

.about__textGroupAChild {
    margin-bottom: 15px;
}
